<template>
  <div class="pay-wrap">
    <app-header
      title="用户结算"
      showBack="true"
    ></app-header>
    <div class="pay-main">
      <h2>您的订单提交成功</h2>
      <div class="info">
        <div class="info-item">
          <span class="l">订单号:</span> <span class="r">{{orderID}}</span>
        </div>
        <div class="info-item">
          <span class="l">收货信息:</span> <span class="r">{{order.address}}</span>
        </div>
        <div class="info-item">
          <span class="l">收货人:</span> <span class="r">{{order.address_name}}</span>
        </div>
        <div class="info-item">
          <span class="l">联系电话:</span> <span class="r">{{order.tel}}</span>
        </div>
        <div class="info-item">
          <span class="l">商品名称:</span> <span class="r">{{order.mall_name}}</span>
        </div>
      </div>
      <div class="price">
        <span>金额:</span>
        <span style="font-size: 16px; color: brown">￥{{order.total_fee}}</span>
      </div>
      <div class="pay-cont">
        <div class="wechat" @click="wechat">
          <i class="fa fa-weixin we" aria-hidden="true"></i>
          <span>微信支付</span>
          <i class="fa fa-chevron-right icon f-r" aria-hidden="true"></i>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  /* eslint-disable */
import {getOrderInfo, tenPayInWechat} from '../../../api/mall'
import {getLocalStorage, isWechat} from '../../../lib/util'
import {initWechatSDK} from '../../../assets/js/wechat'
import AppHeader from '../../../components/header/header.vue'
export default {
  name: 'Mall-Pay',
  data () {
    return {
      order: '',
      orderID: ''
    }
  },
  created () {
    this.getDatas()
    if (isWechat) {
      let urlArr = window.location.href.split('=')
      if (!urlArr[1]) {
        window.location.href = window.location.href + '?refresh=true'
      }
      initWechatSDK('http://m.truckgogo.com/mall/pay/?kjljkjklj')
    }
  },
  mounted () {
    window.onerror = function (err) {
      alert(err)
    }
  },
  methods: {
    getDatas () {
      let orderID = JSON.parse(getLocalStorage('orderInfos'))
      this.$set(this, 'orderID', orderID.order_no)
      getOrderInfo(orderID.order_no)
        .then(data => {
          if (data.state) {
            this.$set(this, 'order', data.data.info)
          } else {
            this.$toast(data.errormsg || '获取订单信息失败')
          }
        })
        .catch(err => console.log(err))
    },
    wechat () {
      let o = this.orderID.toString() + '222'
      let self = this
      if (isWechat()) {
        wx.ready(() => {
          tenPayInWechat(o, this.order.mall_name)
            .then(datas => {
              if (datas.state === 0) return this.$toast(datas.errormsg)
              let data = datas.data
              function onBridgeReady(){
                WeixinJSBridge.invoke(
                  'getBrandWCPayRequest', {
                    "appId": data.appId,
                    "timeStamp": String(data.timeStamp),
                     "nonceStr": data.nonceStr,
                     "package": data.package,
                     "signType": data.signType,
                     "paySign": data.paySign
                  },
                  function(res){
                    if(res.err_msg == "get_brand_wcpay_request:ok" ) {
                      self.$toast('支付成功')
                      self.$router.push('/order')
                    }
                  }
                );
              }
              if (typeof WeixinJSBridge == "undefined"){
                if( document.addEventListener ){
                  document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
                }else if (document.attachEvent){
                  document.attachEvent('WeixinJSBridgeReady', onBridgeReady);
                  document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
                }
              }else{
                onBridgeReady();
              }
            }).catch(err => alert('出错了' + err))
        })
      }
    }
  },
  components: {
    AppHeader
  }
}
</script>

<style lang="less" scoped>
  .pay-wrap{
    height: 100%;
    background-color: #fff;
    .pay-main{
      padding: 50px 1rem 0 1rem;
      border-top: 1px solid #e0e0e0;
      h2{
        text-align: center;
        margin: 0;
        padding-bottom: .6rem;
      }
      .info{
        border-top: 1px solid #e0e0e0;
        font-size: 13px;
        border-bottom: 1px solid #eee;
        .info-item{
          margin-bottom: 5px;
          color: #333;
          .l {
            display: inline-block;
            width: 85px;
            text-align: left;
          }
          .r{
            display: inline-block;
          }
        }
      }
      .price{
        padding: .6rem;
        border-bottom: 1px solid #eee;
      }
      .pay-cont{
        padding: .4rem;
        .wechat {
          padding: .5rem 0;
          position: relative;
          border-bottom: 1px solid #eee;
          .we {
            font-size: 2rem;
            color: #4caf50;
            vertical-align: middle;
          }
          span{
            vertical-align: middle;
          }
          .f-r{
            position: absolute;
            right: 0;
            top: 30%;
            color: #999;
          }
        }
      }
    }
  }
</style>



